class WFSfixednav {
    constructor(config){
        this.config = config;
       
        this.options = {
            conteiner: '.wfs-nav-menu',
            class: 'start-fixed',
            boxNav: '#header',
            marginNav: 0
        }
        
        if(typeof config === 'object'){
            this.options = {...this.options, ...config}
        }

        this.init();
    }
    
    init(){
        this.#validateOption();
    }

    #validateOption(){
        this.conteiner = document.querySelector(this.options.conteiner);
        
        if(this.conteiner){
            console.log(`!!! START - WFSfixednav !!!`);
            this.boxNav = document.querySelector(this.options.boxNav);
            this.boxNavHeight = 0;
            if(this.boxNav) this.boxNavHeight = this.boxNav.getBoundingClientRect().height;
            this.positionY = this.conteiner.getBoundingClientRect().top + window.scrollY - this.options.marginNav - this.boxNavHeight;
            this.windowWidth = window.innerWidth;
            this._time = '';
            this.#checkPosition();
            this.#createWindowEvents();
        }else{
            console.log(`!!! WFSfixednav : no items found !!!`);
        }
    }

    startFixed(){
        if(!this.conteiner.classList.contains(this.options.class)) this.conteiner.classList.add(this.options.class);
    }

    endFixed(){
        if(this.conteiner.classList.contains(this.options.class)) this.conteiner.classList.remove(this.options.class);
    }

    #checkPosition = () => {
        const _this = this;

        if(window.scrollY >= _this.positionY)  _this.startFixed();
        else _this.endFixed();
    }
    
    #checkRsize(){
        this.endFixed();
        if(this.boxNav) this.boxNavHeight = this.boxNav.getBoundingClientRect().height;
        this.positionY = this.conteiner.getBoundingClientRect().top + window.scrollY - this.options.marginNav - this.boxNavHeight;
        this.#checkPosition();
    }

    #resizeWindow = () => {
        const _this = this;
        if(window.innerWidth != _this.windowWidth){
            clearTimeout(_this._time);
            _this._time = setTimeout(() => _this.#checkRsize, 1000);
        }
    }

    #createWindowEvents(){
        window.addEventListener("resize", this.#resizeWindow);
        window.addEventListener("scroll", this.#checkPosition);
    }

    #destroyWindowEvents(){
        window.removeEventListener("resize", this.#resizeWindow);
        window.removeEventListener("scroll", this.#checkPosition);
    }

    destroy(){
        if(this.conteiner){
            this.#destroyWindowEvents();
            this.endFixed();
        }
    }
}